* {
  font-family: Helvetica, Arial, sans-serif;

  box-sizing: border-box;
}
.container {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}
h2 {
  background: lightblue;
  border-radius: 50%;
  padding: 10px;
  width: 50px;
  animation: moveIn 1s ease-out;
  animation-fill-mode: backwards;
}


@keyframes moveIn {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }
  80% {
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
body {
  padding: 60px;
}
input {
  margin-top: 20px;
  width: 100%;
  font-size: 1.2rem;
  color: grey;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  transition: all 0.25;
}
.card:hover {
  transform: translateY(-10px);
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.8);
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(10rem, auto);
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img {
  max-width: 170px;
}
